import React, { useEffect, useState } from "react";
import Base from "../Base";
import { Button, Col, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  createServiceQnAData,
  deleteServiceQnAData,
  getServiceInfo,
  getServiceQnA,
} from "../reducers/serviceReducer";
import { useParams } from "react-router-dom";

import AddServicesQnAComponentModal from "../shared-components/AddServicesQnAComponentModal";
import ProfileServiceQnASection from "../shared-components/ProfileServiceQnASection";
import AnimatedElement from "../shared-components/AnimatedElement";

const ServicesQnA = () => {
  // const servicesQnAList = [
  //   {
  //     _id: 1,
  //     serviceId: 1,
  //     active: true,
  //     question: "Question 1",
  //     userId: 122,
  //     answer:
  //       "An analytical senior-level software developer with deep expertise in UI/UX design and Frontend development posses 12+ year hands on experience. Versed in agile development techniques. Serviceed in requirements analysis and project documentation. Expertise in current and emerging trends and techniques. Proficient in design, coding and developing the new program",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 2,
  //     active: true,
  //     serviceId: 1,
  //     question: "Question 2",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 3,
  //     active: true,
  //     serviceId: 1,
  //     question: "Question 3",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 4,
  //     active: true,
  //     serviceId: 1,
  //     question: "Question 4",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  // ];

  const { serviceId } = useParams();

  const [serviceData, setServiceData] = useState(null);
  const [serviceQnAList, setServiceQnAList] = useState(null);

  const { userdetails } = useSelector((state) => state.user);
  const [addQnAsModal, setAddQnAModal] = useState(false);
  const [updateQnA, setUpdateQnA] = useState(true);
  const formFieldTypes = ["question-text-require", "answer-quill-optional"];
  const [formFieldValues, setFormFieldValues] = useState({});
  const dispatch = useDispatch();

  const getServiceQnAList = async (serviceId) => {
    const data = await dispatch(getServiceQnA({ serviceId }));
    console.log(data);
    if (!data.payload.error) {
      await setServiceQnAList(data.payload);
    } else {
      await setServiceQnAList(null);
    }
  };

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      createServiceQnAData({
        id,
        active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempServices = [...serviceQnAList];
      await serviceQnAList.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempServices[i] = data.payload;
        }
      });
      setServiceQnAList(tempServices);
    }
  };

  const closeModal = () => {
    setAddQnAModal(false);
    setUpdateQnA(true);
    setFormFieldValues({});
  };
  const openModal = () => {
    setAddQnAModal(true);
    setUpdateQnA(false);
  };

  const editQnA = async (question, answer, id) => {
    await setFormFieldValues({
      ...formFieldValues,
      question,
      answer,
      id,
    });
    setAddQnAModal(true);
    setUpdateQnA(false);
  };

  const getService = async (serviceId) => {
    const data = await dispatch(getServiceInfo({ _id: serviceId }));
    console.log(data);
    if (!data.payload.error) {
      await setServiceData(data.payload);
      await getServiceQnAList(serviceId);
    } else {
      await setServiceData(null);
    }
  };

  const deleteServices = async (id) => {
    await dispatch(deleteServiceQnAData({ id: id, token: userdetails.token }));
    getService(serviceId);
  };

  useEffect(() => {
    getService(serviceId);
  }, [userdetails, updateQnA]);

  return (
    <Base>
      {serviceData && (
        <div className="container p-md-5 ">
          <h4 className="p-color pt-3">{serviceData.serviceName}:</h4>
          <div className="t-back2 p-3">
            <Row style={{ position: "sticky", top: "60px", zIndex: 9 }}>
              <Col className="d-flex justify-content-end">
                <Button
                  className="sp-btn-secondary rounded-0 px-5 mb-4 mb-md-0"
                  onClick={openModal}
                >
                  Add New Question
                </Button>
              </Col>
            </Row>
            {serviceQnAList &&
              serviceQnAList.map((item, i) => (
                <AnimatedElement key={item._id}>
                  <ProfileServiceQnASection
                    item={item}
                    updateStatus={updateStatus}
                    editQnA={editQnA}
                    deleteServices={deleteServices}
                    index={i + 1}
                  />
                </AnimatedElement>
              ))}
          </div>
        </div>
      )}
      {addQnAsModal && (
        <AddServicesQnAComponentModal
          setAddQnAModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
          serviceId={serviceId}
        />
      )}
    </Base>
  );
};

export default ServicesQnA;
